import { Search } from '@material-ui/icons'
import ShoppingCartOutlinedIcon from '@material-ui/icons/ShoppingCartOutlined';
import React from 'react'
import { Link } from 'react-router-dom';
import { useStateValue } from '../StateProvider';
import './Header.css'

function Header() {

    const [{cart}, dispatch] = useStateValue();

    return (
        <div className="header">

                <Link to="/">
            <div className="header__logoContent">
            
            <img
            className="header__logo"
            src="//laz-img-cdn.alicdn.com/images/ims-web/TB1F29NfwZC2uNjSZFnXXaxZpXa.png" alt=""
            />
            
            </div>
            </Link>

                <div className="header__search">
                     <input className="header__input" />
                     <Search 
                     className="inputIcon"
                     style ={

                         {color: 'white', 
                         background: '#f57224 ', 
                         padding: '14',
                         border: 'none'
                         }
                        
                        } 
                     
                     />
                </div>
                
                <Link to="/cart">

                    <div className="header__cart">
                        
                <ShoppingCartOutlinedIcon className="header__shoppingCart" 
                style ={{
                    fontSize:'35px',
                    color: '#212121'
                }}

                />

                <div className="header__cartUpdate">
                <p> {cart?.length}</p>
                </div>
               {/* <p> {cart?.length}</p> */}
                    </div>
               
                </Link>

        



                <img className="header__appDownload" src="//laz-img-cdn.alicdn.com/images/ims-web/TB1M6wENNYaK1RjSZFnXXa80pXa.png" alt=""/>


        </div>
    )
}

export default Header
