import './App.css';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import HeaderTop from './components/HeaderTop'
import Header from './components/Header'
import Cart  from './components/Cart'
import Home from './components/Home'
import ProductPage from './components/ProductPage';
import Login from './components/Login'
import Payment from './components/Payment'
import { useStateValue } from './StateProvider';
import { useEffect } from 'react';
import { auth } from './firebase';
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import Orders from './components/Orders';

const promise = loadStripe('pk_test_51DqeK6ITE6sxes7uhdUBzimu8jbiijP4wNz5iXZV9OWPFbVI59IUc6NLNJfQO8j7PzGnDejNyqkPGncLuUzSVVvw00fgH8MHiA')

function App() {

  const [{}, dispatch] = useStateValue();

  // pk_test_51DqeK6ITE6sxes7uhdUBzimu8jbiijP4wNz5iXZV9OWPFbVI59IUc6NLNJfQO8j7PzGnDejNyqkPGncLuUzSVVvw00fgH8MHiA

  useEffect(() => {
    // will only run once when the app component loads...

    auth.onAuthStateChanged((authUser) => {
      console.log("THE USER IS >>> ", authUser);

      if (authUser) {
        // the user just logged in / the user was logged in

        dispatch({
          type: "SET_USER",
          user: authUser,
        });
      } else {
        // the user is logged out
        dispatch({
          type: "SET_USER",
          user: null,
        });
      }
    });
  }, []);


  return (

    <Router>
    <div className="app">

    <HeaderTop className="app__headerTop" />

<Header />
      <Switch>

        <Route path="/orders">
          <Orders />
        </Route>

        <Route path="/login">
         <Login />
        </Route>

        <Route path="/payment">
            <Elements stripe={promise}>
            <Payment />
            </Elements>
            
        </Route>

      <Route path="/cart">
            <Cart />
          </Route>

        <Route path="/:url/:productId">

          <ProductPage />

        {/* {singleProduct.map(({id, price, image, title}) => (

<SingleProduct

    price={price}
    title={title}
    image={image}
    key={id}
/>

))}  */}
            

        </Route>


      {/* <Route path="/cart">
            <Cart />
          </Route> */}

           <Route path="/">
             <Home />
       
          </Route> 
        
        </Switch>
    </div>

    </Router>

  );
}

export default App;
