import React, { useEffect, useState } from 'react'
import { useStateValue } from '../StateProvider'
import { getProductTotal } from '../reducer';
import './Payment.css'
import { CardExpiryElement, CardCvcElement, CardNumberElement, useElements, useStripe } from '@stripe/react-stripe-js';
import axios from '../axios';
import { useHistory } from 'react-router-dom';
import db from '../firebase';
import CurrencyFormat from 'react-currency-format';


function Payment() {

    const [{cart, user}, dispatch] = useStateValue();

    const history = useHistory();

    const stripe = useStripe();
    const elements = useElements();

    const [succeeded, setSucceeded] = useState(false);
    const [processing, setProcessing] = useState("");
    const [error, setError] = useState(null);
    const [disabled, setDisabled] = useState(true);
    const [clientSecret, setClientSecret] = useState(true);

    useEffect(() => {
      // Generate the special strpe secret which allow us to charge the customers

      const getClientSecret = async () => {

        const response = await axios({
          method: 'post',
          // Strip expects the total in a currencies subunits
          url: `/payments/create?total=${getProductTotal(cart) * 100}`
        });
        setClientSecret(response.data.clientSecret);
      }

      getClientSecret();
    }, [cart])


  

    console.log('the Secret is >>>', clientSecret)
    console.log("cart is >>>", cart)


    const handleSubmit = async (e) => {

      e.preventDefault();
      setProcessing(true)

      const payload = await stripe.confirmCardPayment(clientSecret, {
        payment_method: {
          card: elements.getElement(CardNumberElement, CardExpiryElement, CardCvcElement)
        }
      }).then(({paymentIntent})=>{
        // paymentintent = payment confirmation


        db
        .collection('users')
        .doc(user?.uid)
        .collection('orders')
        .doc(paymentIntent.id)
        .set({

          cart: cart,
          amount: paymentIntent.amount,
          created: paymentIntent.created
          
        })


        setSucceeded(true);
        setError(null);
        setProcessing(false);

        dispatch({
          type: 'EMPTY_CART'
        })

        history.replace('/orders')
      })
    }

    const handleChange = e => {

      setDisabled(e.empty);
      setError(e.error? e.error.message: "");
    }

    return (
        <div className="payment">

                <div className="payment__checkout">


      <h2>Payment Method</h2>

      <form
      onSubmit={handleSubmit}
      className="payment__card" >
      <label>
        Card number
        <CardNumberElement
        onChange={handleChange}
        className="card__number"
         
        />
      </label>

      <div className="card__exp">
      <label>
        Expiration date
        <CardExpiryElement
         onChange={handleChange}
        />
      </label>
      <label>
        
        <CardCvcElement className="cart__Cvc"
        onChange={handleChange}
        />
      </label>
      </div>

 
  <div className="subtotal">
  <CurrencyFormat
        renderText={(value) => (
          <>
            <p>
             
              Subtotal ({cart.length} items): <strong>{value}</strong>
            </p>
           
          </>
        )}
        decimalScale={2}
        value={getProductTotal(cart)} 
        displayType={"text"}
        thousandSeparator={true}
        prefix={"$"}
      />
                    

                    </div>

      <button type="submit" disabled={processing || disabled || succeeded}>
        <span>{processing ? <p> Processing </p> : 
        "Pay Now"}</span>
      </button>

      {/* Erorr */}
      {error && <div>{error}</div>}
    </form>

                    
                </div>

                <div className="payment__orderSummary">
                <h4>Order Summary</h4>



                    <div className="subtotal">
                    <CurrencyFormat
        renderText={(value) => (
          <>
            <p>
    
              Subtotal ({cart.length} items): <strong>{value}</strong>
            </p>
        
          </>
        )}
        decimalScale={2}
        value={getProductTotal(cart)} 
        displayType={"text"}
        thousandSeparator={true}
        prefix={"$"}
      />

                    </div>
                                    </div>
            

        </div>
    )
}

export default Payment
