import { Button } from '@material-ui/core'
import React, { useState } from 'react'
import { useHistory } from 'react-router-dom';
import { auth, provider } from '../firebase';
import { useStateValue } from '../StateProvider';
import firebase from 'firebase'
import './Login.css'

function Login() {

    const [{user}, dispatch] = useStateValue();
    const [email, setEmail]= useState("");
    const [password, setPassword] = useState("");
    
    var provider2 = new firebase.auth.FacebookAuthProvider();

    const history = useHistory();
    const history2 = useHistory();

    const signIn = e => {
        e.preventDefault();

        auth
            .signInWithEmailAndPassword(email, password)
            .then(auth => {
                history.push('/')
            })
            .catch(error => alert(error.message))
    }


    const signInwithGoogle = (e) => {

        e.preventDefault();

        auth.signInWithPopup(provider)
        .then(result => {
            history.push('/')

            dispatch({
                type: 'SET_USER',
                user: result.user
            })
        }).catch(erro => {
            alert(erro.message);
        })
    }


    const signInwithFacebook = (e) => {

        e.preventDefault();

        auth.signInWithRedirect(provider2)
        history2.push('/')
        .then(res => {
            // history2.push('/')

            dispatch({
                type: 'SET_USER',
                user: res.user
            }).catch(error => alert(error.message))
        })
    }


    const register = e => {
        e.preventDefault();

        auth.createUserWithEmailAndPassword(email, password)
        .then((auth) =>{
            if(auth){
               history.push('/')
            }
        }).catch(error => alert(error.message))
    }
  

    return (
        <div className="login">
            <h3>Welcome to Daraz! Please login.</h3>
            
            <div className="login__form">
            <form action="">

                <div className="form__inputFields">
                <label>Email</label>
                <input onChange={e => setEmail(e.target.value)} value={email} type="text"/>
                <label>Password</label>
                <input onChange={e => setPassword(e.target.value)} value={password} type="password" name="" id=""/>

                <button type="submit"
                 className="form__register"
                 onClick={register}
                 >
                     Create an Account</button>
                </div>

                <div className="form__buttons">
                    <Button 
                    
                    className="logInWithEmailPass" 
                    onClick={signIn}
                    variant="contained" color="secondary">LOGIN</Button>
                    <p>Or, login with</p>
                    <Button onClick={signInwithFacebook} className="logInwithFacebook" variant="contained" color="secondary">Facebook</Button>
                    <Button onClick={signInwithGoogle} className="logInWithGoogle" variant="contained" color="secondary">G+ Google</Button>
                </div>

                </form>
            </div>
        </div>
    )
}

export default Login
