import React from 'react'
import { Link } from 'react-router-dom';
import { auth } from '../firebase';
import { useStateValue } from '../StateProvider'
import "./HeaderTop.css"

function HeaderTop() {

    const [{user}, dispatch]= useStateValue();

    const handleAuthenticaton = () => {
        if (user) {
         auth.signOut();
        }
      }

    console.log(user?.displayName)

    return (
        <div className="headertop">

            <div className="headertop__items">
            <p className="headertop__savemore">SAVE MORE ON APP</p>
            <p>DARAZ AFFILIATE PROGRAM</p>
            <p>SELL ON DARAZ</p>
            <p>CUSTOMER CARE</p>
            <p>TRACK MY ORDER</p>
            <p>{user?.displayName || user?.email}</p>

            <Link to={!user && './login'}>
            <p onClick={handleAuthenticaton}> 

                {user ? 'Sign Out' : 'Sign In'}

            </p>
            </Link>

            {/* <Link to={!user && '/login'}>
          <div onClick={handleAuthenticaton} className="header__option">
            <span className="header__optionLineOne">Hello {!user ? 'Guest' : user.displayName}</span>
            <span className="header__optionLineTwo">{user ? 'Sign Out' : 'Sign In'}</span>
          </div>
        </Link> */}
            </div>

       
            
                        
        </div>
    )
}

export default HeaderTop
