export const initialState ={
    cart: [],
    user: null
};



export const getProductTotal = (cart) => 
            cart?.reduce((amount, product) => product.price +  amount, 0);

         
const reducer = (state, action) => {

  

    console.log(action);

    switch (action.type){

        case 'ADD_TO_CART': 

        return{
            ...state,
            cart: [...state.cart, action.product]
        }

        case 'EMPTY_CART':
            return {
              ...state,
              cart: []
            }

        case 'REMOVE_FROM_CART':
        const index = state.cart.findIndex(
            (CartProduct)  => CartProduct.id === action.id
        );

        let newCart = [...state.cart];

        if(index >= 0){
                newCart.splice(index, 1);
        } else {
            console.warn(
                `cant remove the product (id: ${action.id}) as its not in the cart`
            )
        }

        return {
            ...state,
            cart: newCart
        }

        case 'SET_USER':
            return{
                ...state,
                user: action.user
            }

      default:
          return state;
          
    }
};

export default reducer;