import { Button } from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import db from '../firebase';
import { useStateValue } from '../StateProvider'
import './SingleProduct.css'

function SingleProduct({id, price, image, title, url}) {

    const [{cart}, dispatch] = useStateValue();

    const addToCart = () => {

        dispatch({
            type: 'ADD_TO_CART',
            product:{
                id: id,
                title: title,
                image: image,
                price: price,
                url: url,
            }
        })
    }

    return (
        <div className="singleproduct">
         <img src={image} alt=""/>

         <div className="singleproduct__info">
             <p className="singleproduct__desc">
             {title}
             </p>
             <p className="singleproduct__price">Rs {price}</p>

             <div className="singleproduct__buttons">

             <Button
             className="singleproduct__buy"
            variant="contained" color="secondary"

            >Buy Now</Button>

                <Button
                className="singleproduct__cart"
                onClick={addToCart}
                variant="contained" color="secondary"

            >Add to Cart</Button>
             </div>

       

         </div>
        </div>
    )
}

export default SingleProduct


